.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.8);
}

.popup-menu {
    max-width: 40%;
    background-color: white;
    box-sizing: border-box;
    padding: 1px;
    color: aliceblue;
    margin-top: 250px;
    margin-left: 30%;
    margin-right: 30%;
}

.text2 {
    text-align: center;
    text-decoration: underline;
    color: burlywood;
    font-size: 150%;
}

.Close {
    display: inline;
    float: right;
    padding: 0%;
    margin: 0%;
    border: 1px solid red;
    cursor: pointer;
    color: white;
    background-color: rgba(248, 21, 40, 0.8);
}

.Close:hover {
    background-color: rgba(248, 21, 40);
    opacity: 1;
}

.tick {
    color: chartreuse;
    font-size: 200%;
    border-radius: 50%;
    border: 1px double green;
}