body {
  background-color:burlywood;
  font-family: Arial, Helvetica, sans-serif;

}

h2 {
  text-align: center;
  color: chartreuse;
  font-family:Arial, Helvetica, sans-serif;
}

.Signup-form {
  max-width: 450px;
  border-radius: 20px;
  margin: auto;
  background-color: rgba(0,0,0,0.8);
  box-sizing: border-box;
  padding: 40px;
  color: aliceblue;
  margin-top: 100px;
}

input[type=text],
input[type=password] {
  width: 100%;
box-sizing: border-box;
padding: 12px 10px;
background-color: rgba(0,0,0,10);
outline: none;
border: none;
border-bottom: 1px solid grey;
color: rgb(248, 243, 243);
border-radius: 5px;
margin: 5px;
font-weight: bold;
}
input[type=Submit]{
  width: 100%;
  box-sizing: border-box;
  padding: 10px ;
  margin-top: 30px;
  outline: none;
  border: none;
  background-color:chocolate;
  opacity: 0.3;
  border-radius: 20px;
  font-size: 20px;
  color: aliceblue;
  cursor: pointer;
  transition: 1s;
}
input[type=Submit]:hover{
  opacity: 1;
}
span{
  color: yellowgreen;
  font-size: medium;
}